.franchising {
  background: $clr-red;
  padding: 135px 0 120px;

  &__heading {
    width: calc(100% + 30px);
    margin-left: -70px;
    background: $bg-grey;
    height: 200px;
    color: $clr-white;
    padding-left: 70px;
    position: relative;
    z-index: 5;

    h2 {
      font-family: $font;
      font-size: 165px;
      font-weight: bold;
      margin-top: -60px;
      position: relative;
      display: inline-block;
    }
    p {
      display: block;
      padding-left: 265px;
      color: $clr-white;
      padding-top: 20px;
      font-family: $font;
      font-size: 14px;
    }
  }
  
  &__content {
    padding: 110px 70px 30px 100px;
    background: $clr-white;
    position: relative;
    margin-top: -50px;
    &:after {
      content: '';
      width: 100%;
      clear: both;
      display: block;
    }

    .col {
      width: 40%;
      padding-right: 10%;
      float: left;
      margin-bottom: 70px;

      h3 {
        font-family: $font;
        color: $clr-red;
        font-size: 30px;
        font-weight: bold;
        display: inline-block;
        width: 100%;
        margin-bottom: 60px;

        span {
          display: inline-block;
          width: 100%;
          font-size: 24px;
        }
      }

      p {
        font-size: 14px;
        font-family: $font;
        color: $clr-white-grey;
      }

      ul {
        li {
          text-transform: uppercase;
          font-size: 14px;
          font-family: $font;
          color: $clr-white-grey;
          position: relative;
          margin-bottom: 20px;
          display: inline-block;
          padding-left: 38px;
          
          &:before {
            content: '';
            display: block;
            position: absolute;
            left: 0;
            top: 3px;
            width: 8px;
            height: 8px;
            background: $clr-red;

          }
        }
      }

      &:first-child {
        width: 60%;
        padding-right: 22%;
      }
    }
  }

  .questions {
    width: 60%;
    font-family: $font;
    font-size: 30px;
    font-weight: bold;
    color: $bg-grey;
    line-height: 1.5;
    float: left;
    
    span.phone {
      display: inline-block;
      width: 100%;
      color: $clr-red;
    }
  }

  &__action {
    float: right;
    width: 40%;
    text-align: center;
    position: relative;
    margin-top: -13px;
    display: flex;
    flex-direction: column;
    align-items: center;

    .btn {
      width: 260px;
      padding: 0;
      display: block;
    }
  }

}






























