.addresses {
  .map-wrap {
    position: absolute;
    height: 100%;
    width: calc((100% - 1200px)/2 + 1200px*0.6 - 3px);
    top: 0;
    right: 0;
    overflow: hidden;
    img {
      display: block;
    }
    #map {
      height: 415px;
      width: 100%;
    }
  }
  &__content {
    width: 40%;
    float: left;
    padding: 100px 30px 40px;

    span {
      font-family: $font;
      color: $clr-red;
      font-size: 14px;
      display: inline-block;
      width: 100%;
      font-weight: bold;
      margin-bottom: 15px;
      &:last-child {
        padding-bottom: 30px;
        position: relative;
        &:after {
          content: '';
          width: 33px;
          height: 5px;
          background: $clr-red;
          position: absolute;
          left: 0;
          bottom: 0;
        }
      }
    }
    h3 {
      color: $bg-dark;
      font-family: $font;
      font-size: 32px;
      font-weight: bold;
      display: inline-block;
      width: 100%;
      margin-bottom: 35px;
    }
    h4 {
      color: $bg-dark;
      font-family: $font;
      font-size: 14px;
      font-weight: bold;
      display: inline-block;
      width: 100%;
      margin-bottom: 47px;
    }
    ul {
      margin-bottom: 45px;
      li {
        font-family: $font;
        font-size: 14px;
        display: inline-block;
        padding-left: 25px;
        position: relative;
        width: 100%;
        margin-bottom: 20px;
        &:before {
          content: '';
          display: block;
          position: absolute;
          width: 5px;
          height: 5px;
          background: $clr-red;
          left: 0;
          top: 5px;
        }
      }
    }
  }
  &__separator {
    width: 30px;
    height: 20px;
    position: relative;
    margin-bottom: 45px;
    &>div {
      width: 100%;
      height: 100%;
    }
    &:after,
    &:before,
    &>div:after,
    &>div:before {
      content: '';
      display: block;
      position: absolute;
      width: 33.33%;
      height: 50%;
      background: $clr-red;
    }
    &:after {
      height: 100%;
      top: 0;
      left: 0;
    }
    &:before {
      width: 66.66%;
      top: 50%;
      left: 0;
    }
    &>div:after {
      top: 0;
      left: 66.66%;
    }
  }
}




















