.clusters {
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.cluster {
  position: absolute;
  &,
  &>div,
  &>div>div {
    &:before,
    &:after {
      content: '';
      display: block;
      position: absolute;
      background: $clr-white;
    }
  }

  &.dot {
    width: 10px;
    height: 10px;
    background: $clr-white;
  }

  &_1 {
    width: 22px;
    height: 22px;
    top: 21%;
    left: 7%;
    transform: translateZ(290px);
    &:before {
      width: 100%;
      height: 50%;
      top: 0;
      left: 0;
    }
    &:after {
      width: 50%;
      height: 100%;
      top: 0;
      left: 0;
    }
  }
  &_2 {
    top: 78%;
    left: 7.8%;
    transform: translateZ(150px);
  }
  &_3 {
    width: 74px;
    height: 40px;
    left: 17%;
    top: 48%;
    transform: translateZ(400px);
    z-index: 50;
    &:before {
      width: 68.91%;
      height: 25%;
      left: 0;
      bottom: 0;
    }
    &:after {
      width: 41.9%;
      height: 25%;
      left: 20.2%;
      top: 50%;
    }
    &>div {
      &:before {
        width: 13.5%;
        height: 25%;
        left: 6.7%;
        top: 25%;
      }
      &:after {
        width: 13.5%;
        height: 25%;
        left: 57%;
        top: 25%;
      }
      &>div {
        position: absolute;
        width: 30%;
        height: 25%;
        top: 0;
        left: 70%;
        background: $clr-white;
      }
    }
  } // 400px - Z
  &_4 {
    width: 48px;
    height: 32px;
    left: 31.2%;
    top: 26.4%;
    transform: translateZ(400px);
    &:before {
      width: 33.33%;
      height: 50%;
      left: 0;
      top: 0;
    }
    &:after {
      width: 33.33%;
      height: 50%;
      right: 0;
      top: 0;
    }
    &>div:before {
      width: 33.33%;
      height: 50%;
      left: 33.33%;
      top: 50%;
    }
  } // 400px - Z
  &_5 {
    width: 20px;
    height: 30px;
    left: 57.3%;
    top: 40.3%;
    transform: translateZ(290px);
    &:before {
      width: 50%;
      height: 33.33%;
      left: 0;
      top: 0;
    }
    &:after {
      width: 50%;
      height: 66.66%;
      left: 50%;
      top: 33.33%;
    }
  }
  &_6 {
    left: 69%;
    top: 16%;
    transform: translateZ(150px);
  }
  &_7 {
    right: 25.5%;
    top: 44%;
    width: 32px;
    height: 32px;
    transform: translateZ(400px);
    &:before {
      width: 100%;
      height: 50%;
      left: 0;
      top: 0;
    }
    &:after {
      width: 50%;
      height: 100%;
      left: 50%;
      top: 0;
    }
  } //400px - Z
  &_8 {
    right: 22%;
    top: 48%;
    transform: translateZ(150px);
  }
  &_9 {
    width: 30px;
    height: 30px;
    right: 17.4%;
    top: 33.3%;
    transform: translateZ(290px);
    &:before {
      width: 33.33%;
      height: 33.33%;
      left: 0;
      top: 0;
    }
    &:after {
      width: 33.33%;
      height: 66.66%;
      top: 33.33%;
      left: 33.33%;
    }
    &>div:before {
      width: 66.66%;
      height: 33.33%;
      top: 66.66%;
      left: 33.33%;
    }
  }
  &_10 {
    height: 28px;
    width: 19px;
    right: 10%;
    bottom: 20.5%;
    transform: translateZ(290px);
    &:before {
      width: 50%;
      height: 33.33%;
      top: 0;
      left: 0;
    }
    &:after {
      width: 50%;
      height: 33.33%;
      bottom: 0;
      left: 0;
    }
    &>div:before {
      width: 50%;
      height: 33.33%;
      top: 33.33%;
      left: 50%;
    }
  }
}