.home {
  width: 100%;
  height: 50vmax;
  position: relative;
  background: $clr-red;

  #homeSection {
    width: 100%;
    height: 100%;
    position: absolute;
    perspective: 4000px;
  }

  .site-width {
    position: relative;
    z-index: 5;
  }

  &__cover {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background: $clr-red;
    text-align: center;
    transform-style: preserve-3d;
    transform: translate3d(0,0,0);
    will-change: transform;
    transition: transform ease 200ms;

    svg {
      margin-top: 150px;
      max-width: 40%;
      max-height: calc(100% - 150px);
      fill: $clr-black;
      opacity: .34;
    }

    img {
      opacity: .34;
      width: 100%;
      height: 100%;
      transform: translateZ(50px) scale(1.1);
    }
  }

  &__heading {
    color: #FFF;
    display: block;
    position: absolute;
    width: 100%;
    left: -50px;
    bottom: 200px;
    height: 240px;
    font-weight: bold;
    padding-left: 100px;
    z-index: 10;

    &-cover {
      width: 370px;
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      background: $clr-red;

      &:after {
        content: '';
        position: absolute;
        width: 10px;
        height: 10px;
        background: $clr-red;
        left: 100%;
        top: 100%;
      }
    }

    h1 {
      position: absolute;
      display: block;
      top: -50px;
      font-size: 226px;

      i {
        display: inline-block;
      }

      span {
        display: inline-block;
        white-space: nowrap;
        color: $clr-red;
        margin-left: 305px;
        position: relative;
        top: -152px;
      }
    }
  }

  &__text {
    position: absolute;
    height: 310px;
    width: 100%;
    bottom: 0;
    background: $clr-white;
    padding: 150px 95px 64px 340px;
    display: flex;
    justify-content: space-between;

    p {
      display: inline-block;
      vertical-align: top;
      max-width: 45%;
      width: 100%;
    }

    .cluster {
      &:before,
      &:after {
        background: $clr-red;
      }
      &_5 {
        right: 0;
        left: auto;
        bottom: 133px;
      }
      &_11 {
        width: 20px;
        height: 30px;
        left: 47px;
        bottom: -15px;
        &:before {
          width: 50%;
          height: 100%;
          left: 0;
          top: 0;
        }
        &:after {
          width: 50%;
          height: 33.33%;
          left: 50%;
          top: 33.33%;
        }
      }
    }
  }

}