.footer {
  position: relative;
  width: 100%;
  height: auto;
  padding: 100px 65px 0;

  .slider {
    width: 100%;
    height: 100%;
    position: relative;

    &-controls {
      position: absolute;
      right: 230px;
      top: 65px;
    }

    .slide {
      position: relative;
      width: 100%;
      height: 100%;
      left: auto;
      top: 0;
      right: auto;

      img {
        position: relative;
        width: 100%;
        height: 100%;
        left: auto;
        top: 0;
        right: auto;
      }
    }
  }

  &__content {
    width: calc(100% + 130px);
    position: relative;
    left: -65px;
    display: flex;
    flex-direction: column;
    align-items: center;
    &>div {
      width: 1445px;
      max-width: 100%;
    }
  }
  &__top {
    background: $clr-red;
    color: $clr-white;
    padding: 37px 65px 53px 135px;
    h3 {
      width: 100%;
      display: inline-block;
      font-size: 48px;
      font-family: $font;
      font-weight: bold;
      margin-bottom: 43px;
    }
    p {
      font-family: $font;
      font-size: 14px;
      color: $clr-white;
      padding-right: 265px;
    }
  }
  &__mid {
    background: $bg-grey;
    color: $clr-white;
    padding: 58px 115px 52px 135px;
    p {
      width: calc(100% - 430px);
      display: inline-block;
      font-family: $font;
      font-size: 14px;
      color: $clr-white;
    }
    a {
      font-family: $font;
      font-weight: bold;
      font-size: 36px;
      color: $clr-white;
      float: right;
      transition: all ease 200ms;
      .ico-next {
        display: inline-block;
        margin-left: 15px;
        transition: all ease 200ms;
      }
      &:hover {
        transform: translateX(-15px);
        color: $clr-red;
        .ico-next {
          transform: translateX(15px);
          &:before,
          &:after,
          &>div:before,
          &>div:after {
            background: $clr-red;
          }
        }
      }
    }
  }
  &__bot {
    padding: 60px 0 35px;
    display: flex;
    flex-direction: column;
    align-items: center;
    svg {
      width: 75px;
      fill: $clr-red
    }
    span {
      width: 100%;
      color: $clr-red;
      text-align: center;
      top: 323px;
      text-transform: uppercase;
      font-size: 6px;
      i {
        font-size: 9px;
        position: relative;
        top: 1px;
        margin: 0 2px;
      }
    }
  }

}