h1 {
  font-size: 220px;

  span {
    font-size: 33px;
  }
}

.block-revealer__element {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  pointer-events: none;
  opacity: 0;
}

.heading-main {
  color: $clr-red;
  position: relative;
  padding-right: 20px;
  &:before {
    content: attr(data-hover);
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    overflow: hidden;
    max-width: 0;
    width: 110%;
    color: #fff;
    transform: translate3d(0,0,0);
    will-change: max-width;
    animation-timing-function: ease-in;
    animation-name: show-title;
    animation-delay: 500ms;
    animation-duration: 500ms;
    animation-fill-mode: forwards;
  }
}

@-webkit-keyframes show-title {
  from {max-width: 0;}
  to {max-width: 110%;}
}
@keyframes show-title {
  from {max-width: 0;}
  to {max-width: 110%;}
}