.photo {
  padding: 100px 0 107px;
  overflow-y: hidden;
  height: 800px;
  margin-bottom: 108px;

  .slider-controls {
    position: absolute;
    bottom: 0;
  }

  &:before {
    content: '';
    position: absolute;
    width: 100%;
    height: calc(100% - 107px);
    background: $bg-grey;
    top: 0;
    left: 0;
    z-index: -1;
  }

  &-slider.slider {
    width: 100%;

    .slide {
      justify-content: flex-start;
      position: relative;

      img {
        left: auto;
        right: calc((100% - 1200px)/2 + 1200px*0.3 + 3px);
        position: absolute;
        min-width: calc(100vw * 0.6);
      }
    }
  }

  &-article {
    width: 30%;
    float: right;
    padding-left: 55px;
    padding-top: 87px;

    p {
      color: $clr-white;
      font-family: $font;
      font-size: 16px;
      margin-bottom: 20px;
    }
  }

}