.ico-ok {
  width: 40px;
  height: 30px;
  position: relative;
  &>div {
    width: 100%;
    height: 100%;
  }
  &:before,
  &:after,
  &>div:before,
  &>div:after {
    width: 10px;
    height: 10px;
    position: absolute;
    background: $clr-red;
    content: '';
    display: block;
  }
  &:before {
    left: 0;
    top: 10px;
  }
  &:after {
    left: 10px;
    top: 20px;
  }
  &>div:before {
    left: 20px;
    top: 10px;
  }
  &>div:after {
    left: 30px;
    top: 0;
  }
}

.ico-prev {
  width: 24px;
  height: 20px;
  position: relative;
  &>div {
    width: 100%;
    height: 100%;
  }
  &:before,
  &:after,
  &>div:before,
  &>div:after {
    width: 16.66%;
    height: 20%;
    position: absolute;
    background: $clr-white;
    content: '';
    display: block;
  }
  &:before {
    width: 100%;
    left: 0;
    top: 40%;
  }
  &:after {
    height: 60%;
    left: 16.66%;
    top: 20%;
  }
  &>div:before {
    left: 33.33%;
    top: 0;
  }
  &>div:after {
    left: 33.33%;
    top: 80%;
  }
}
.ico-next {
  width: 24px;
  height: 20px;
  position: relative;
  &>div {
    width: 100%;
    height: 100%;
  }
  &:before,
  &:after,
  &>div:before,
  &>div:after {
    width: 16.66%;
    height: 20%;
    position: absolute;
    background: $clr-white;
    content: '';
    display: block;
  }
  &:before {
    width: 100%;
    left: 0;
    top: 40%;
  }
  &:after {
    height: 60%;
    left: 66.66%;
    top: 20%;
  }
  &>div:before {
    left: 50%;
    top: 0;
  }
  &>div:after {
    left: 50%;
    top: 80%;
  }
}